<template>
  <transition
    name="custom-classes-transition"
    enter-active-class="animated fadeIn"
    leave-active-class="animated fadeOut delay"
  >
    <div id="prompt" class="prompt__wrap" v-show="isShow" @click="close">
      <transition
        name="custom-classes-transition"
        enter-active-class="animated bounceIn delay"
        leave-active-class="animated bounceOut"
      >
      <div class="prompt" v-show="isShow">
        <div class="prompt__title">昵称</div>
        <div class="prompt__input">
          <input type="text" maxlength="12" placeholder="昵称" v-model="name">
        </div>
        <div class="prompt__btn">
          <span class="btn__cancel" @click="cancel">取消</span>
          <span class="btn__sure" @click="sure(3)">确定</span>
        </div>
      </div>
      </transition>
    </div>
  </transition>
</template>

<script>
  export default {
    data() {
      return {
        isShow: false,
        name: ''
      }
    },
    methods: {
      close(event) {
        if(event.target.id == 'prompt') {
          this.isShow = false;
        }
      },

      cancel() {
        this.isShow = false;
      },

      sure() {
        this.isShow = false;
        console.log(this.name);
        return this.name;
      }
    }
  }
</script>


<style lang="scss">
  #prompt {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0, 0.3); 
    display: flex;
    align-items: center;
    justify-content: center;

    .prompt {
      
      background: #fff;
      border-radius: 10px;

      .prompt__title {
        margin: 0.4rem 0.4rem;
        font-size: 17px;
        font-weight: 600;
        text-align: center;
      }

      .prompt__input {
        margin: 0.61rem 0.4rem;
        padding-left: 0.40rem; 
        width: 6.53rem;
        line-height: 1.067rem;
        height: 1.067rem;
        border: 1px solid #eee;

        input {
          width: 100%;
        }
      }

      .prompt__btn {
        border-top: 1px solid #eee;
        font-size: 0;

        .btn__cancel, .btn__sure {
          display: inline-block;
          height: 1.227rem;
          line-height: 1.227rem;
          width: 50%;
          text-align: center;
          font-size: 17px;
        }

        .btn__sure {
          position: relative;
          color: #de7fef;
          &::before {
            content: '';
            position: absolute;
            top: 15%;
            left: 0;
            height: 70%;
            border-left: 1px solid #eee;
          }
        }

        .btn__cancel {
          color: #333333;
        }
      }
    }
  }

  .delay {
    animation-delay: 0.2s;
  }
</style>




