<template>
	<div id="common_header">
		<div class="header-bar">
			<img class="lt-arrow" src="../../assets/arrow.png" @click="$router.go(-1)"/>
			<span class="title">{{title}}</span>
			<span class="header-link" v-if="link" @click="linkClick">{{link}}</span>
			<slot></slot>
		</div>
	</div>
</template>

<script>
	export default {
		props:['title', 'link'],
		methods: {
			linkClick() {
				this.$emit('linkClick');
			}
		}
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";

	#common_header{
		width:100%;
		height: 1.17rem;
		.header-bar{
			@include headerStyle;
			
			.lt-arrow{
				/*@include lt-arrow;*/
				display: inline-block;
				@include lt-arrow;
			}
			.title{
				font-size: 0.48rem;
			}			
			/*.edit{
				position: absolute;
				right: 0.4rem;
				font-size: 0.4rem;
			}*/
			.header-link {
				position: absolute;
				right: 0.4rem;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
</style>


