<template>
  <div id="popup-main" v-if="show">
    <div class="main theCenter">
      <div class="title" v-if="title" v-text="title">标题</div>
      <div class="content" v-if="content" v-text="content">对不起，您购买的产品已经卖完了，请返回购物车重新下单</div>
      <div class="btn" v-if="showCancel">
        <div class="cancel" v-text="cancelText" @click="cancel">取消</div>
        <div class="line1 theCenter"></div>
        <div class="sure" v-text="sureText" @click="sure" >确定</div>
      </div>
      <div class="btn" v-else  v-text="sureText" @click="sure">
        确定
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        showCancel: false,
        show: false,
        cancelText: '取消',
        sureText: '确定',
        title: '',
        content: ''
      }
    },
    // props: {
    //   showCancel: {
    //     type: Boolean,
    //     default: true
    //   },
    //   show: {
    //     type: Boolean,
    //     default: false
    //   },
    //   cancelText: {
    //     type: String,
    //     default: '取消'
    //   },
    //   sureText: {
    //     type: String,
    //     default: '确定'
    //   },
    //   title: {
    //     type: String,
    //     default: '测试'
    //   },
    //   content: {
    //     type: String,
    //     default: '测试内容'
    //   }
    // },
    watch: {},
    methods: {
      showChange(type = true){
        this.$emit('showChange',type);
      },

      sure() {
        this.show = false;
        console.log('你点击的是确定');
      },

      cancel() {
        this.show = false;
        console.log('你点击的是取消');
      }
    }
  }
</script>

<style lang="scss">
  #popup-main{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.5);
		z-index: 1000;
    .theCenter{
      position: absolute;
      top:50%;
      left: 50%;
      transform:translate(-50%,-50%);
    }
    .main{
      width: 7.33rem;
      min-height:3.47rem;
      background: #fff;
      padding: 0.53rem 0.53rem 1.2rem 0.53rem;
      border-radius: 0.13rem;
      text-align: center;
    }
    .title{
      font-size: 0.45rem;
      color: #000;
      margin-bottom: 0.65rem;
      /*font-weight: 600;*/
      letter-spacing: 3px;
    }
    .content{
      font-size: 0.4rem;
      color: #333;
      margin-bottom: 0.65rem;
    }

    .btn{
      width: 100%;
      height: 1.2rem;
      position: absolute;
      bottom: 0;
      left: 0;
      text-align: center;
      font-size: 0.45rem;
      font-weight: 400;
      color: #de7fef;
      line-height: 1.2rem;
      border-top: 1px #ccc solid;
    }
    .cancel,
    .sure{
      width: 50%;
      float: left;
    }
    .cancel{
      color: #333;
    }
    .line1{
      width: 1px;
      height: 70%;
      background-color: #ccc;
    }
}

</style>



